<template>
  <div class="textTerm">
    <span style="min-width: 100px;display: inline-block;text-align: right;padding-right: 10px;">{{titName}}：</span>
    <DatePicker size="large" type="year"
                v-model="cl010"
                @on-change="srldChange()"
                :start-date="new Date()" placement="bottom-end"
                :placeholder="'请输入' + titName" style="width: 200px"></DatePicker>
  </div>
</template>

<script>
import { renderYearTime } from '@/utils/common'
export default {
  name: 'TextTerm',
  data () {
    return {
      /* input内容 */
      cl010: ''
    }
  },
  props: {
    /* 字段标题 */
    titName: {},
    /* 字段key值 */
    titKey: {}
  },
  mounted () {

  },
  methods: {
    srldChange () {
      if (this.cl010 === null || this.cl010 === '') {
        this.$emit('delQueryTrem', this.titKey)
      } else {
        let obj = {
          name: this.titName,
          text: renderYearTime(this.cl010),
          key: this.titKey,
          value: renderYearTime(this.cl010)
        }
        this.$emit('updataQueryData', obj)
      }
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  .cascadeTerm{

  }
</style>
